ul.call-to-action {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  margin: 1rem 0;
  text-transform: uppercase;
}

ul.call-to-action li button {
  background: none;
  color: inherit;
  border: 3px solid;
  border-radius: 5rem;
  font-size: 90%;
  padding: 0.25rem 2rem;
}


li.learn-more-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
}
